"use client";
import React, { useState, useEffect } from "react";
import { ArrowRight } from "lucide-react";
import ImageTrail from "../components/ImageTrail";
import Link from "next/link";

// Hero Section
const HeroSection = () => {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth < 1024);
    };
    checkMobile();
    window.addEventListener("resize", checkMobile);
    return () => window.removeEventListener("resize", checkMobile);
  }, []);

  return (
    <section id="home" className="relative h-screen overflow-hidden">
      {/* Gradient Background for Mobile */}
      {isMobile && (
        <div className="absolute inset-0 bg-linear-to-br from-blue-600 via-purple-600 to-indigo-600 opacity-90 z-1" />
      )}

      {/* Image Trail - Desktop Only */}
      {!isMobile && (
        <ImageTrail
          images={[
            "/heroImages/1.png",
            "/heroImages/2.jpg",
            "/heroImages/3.jpg",
            "/heroImages/4.jpg",
          ]}
        />
      )}

      {/* Dark Overlay for Text Readability - Desktop Only */}
      {!isMobile && (
        <div className="absolute inset-0 bg-linear-to-b from-black/40 via-black/20 to-black/40 z-15" />
      )}

      {/* Hero Content - Always on top */}
      <div className="absolute inset-0 flex items-center justify-center z-20 pointer-events-none">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 text-center pointer-events-auto">
          <h1 className="text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-bold text-white mb-6 leading-tight drop-shadow-2xl">
            Best Printing Services in Afghanistan - We Print What You Think
          </h1>
          <p className="text-lg sm:text-xl md:text-2xl text-white/90 mb-10 max-w-2xl mx-auto drop-shadow-xl">
            Afghanistan's Premier Printing Solutions Since 2002
          </p>
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <Link
              href="/contact"
              className="bg-white text-blue-600 px-8 py-4 rounded-full font-bold text-lg hover:bg-blue-50 transform hover:scale-105 transition-all inline-flex items-center justify-center shadow-xl"
            >
              Get a Quote <ArrowRight className="ml-2" />
            </Link>
            <Link
              href="/portfolio"
              className="border-2 border-white text-white px-8 py-4 rounded-full font-bold text-lg hover:bg-white hover:text-blue-600 transition-all inline-flex items-center justify-center shadow-xl backdrop-blur-sm bg-white/10"
            >
              View Our Work
            </Link>
          </div>
        </div>
      </div>
    </section>
  );
};

export default HeroSection;
